import React, { useState, useEffect } from "react";
import Layout from "@/components/Layout";
import { Table, Button, Modal, Form, InputGroup, FormControl, Dropdown } from "react-bootstrap";
import styles from "../styles/product.module.css";
import PaginationComponent from "@/components/PaginationComponent";

const ProductPage = () => {
  const [products, setProducts] = useState([]);
  const [filteredProducts, setFilteredProducts] = useState([]);
  const [searchQuery, setSearchQuery] = useState("");
  const [currentPage, setCurrentPage] = useState(1);
  const [itemsPerPage] = useState(10);
  const [showModal, setShowModal] = useState(false);
  const [newProduct, setNewProduct] = useState({ name: "", price: "", status: "Active" });

  // Fetch products
  useEffect(() => {
    const fetchProducts = async () => {
      const fetchedProducts = [
        { id: 1, name: "Smartphone", price: "$500", status: "Active", addedDate: "2024-12-20" },
        { id: 2, name: "Laptop", price: "$1200", status: "Active", addedDate: "2024-12-18" },
        { id: 3, name: "Headphones", price: "$150", status: "Inactive", addedDate: "2024-12-15" },
        { id: 4, name: "Camera", price: "$800", status: "Active", addedDate: "2024-12-10" },
      ];
      setProducts(fetchedProducts);
      setFilteredProducts(fetchedProducts);
    };
    fetchProducts();
  }, []);

  // Paginate filtered products
  const paginatedProducts = filteredProducts.slice(
    (currentPage - 1) * itemsPerPage,
    currentPage * itemsPerPage
  );

  const handlePageChange = (page) => setCurrentPage(page);

  // Search functionality
  const handleSearch = (e) => {
    const query = e.target.value.toLowerCase();
    setSearchQuery(query);
    const filtered = products.filter(
      (product) =>
        product.name.toLowerCase().includes(query) || product.price.toLowerCase().includes(query)
    );
    setFilteredProducts(filtered);
    setCurrentPage(1);
  };

  // Open and close modal
  const handleShowModal = () => setShowModal(true);
  const handleCloseModal = () => {
    setShowModal(false);
    setNewProduct({ name: "", price: "", status: "Active" });
  };

  // Handle input changes
  const handleNewProductChange = (e) => {
    const { name, value } = e.target;
    setNewProduct((prev) => ({ ...prev, [name]: value }));
  };

  // Add new product
  const handleAddProduct = () => {
    if (newProduct.name && newProduct.price) {
      const newId = products.length ? products[products.length - 1].id + 1 : 1;
      setProducts([...products, { id: newId, ...newProduct, addedDate: new Date().toISOString().split("T")[0] }]);
      setFilteredProducts([...products, { id: newId, ...newProduct, addedDate: new Date().toISOString().split("T")[0] }]);
      handleCloseModal();
    }
  };

  // Delete product
  const handleDeleteProduct = (id) => {
    const updatedProducts = products.filter((product) => product.id !== id);
    setProducts(updatedProducts);
    setFilteredProducts(updatedProducts);
  };

  return (
    <Layout>
      <div className="container mt-5">
        <h1 className={`${styles.pageTitle} text-center mb-4`}>Product Management</h1>

        {/* Search and Add Product */}
        <div className="d-flex justify-content-between align-items-center mb-4">
          <InputGroup className="w-50">
            <FormControl
              placeholder="Search by product name or price"
              value={searchQuery}
              onChange={handleSearch}
            />
          </InputGroup>
          <Button variant="primary" onClick={handleShowModal}>
            Add Product
          </Button>
        </div>

        {/* Product Table */}
        <Table striped bordered hover responsive>
          <thead className={styles.tableHeader}>
            <tr>
              <th>Sl No.</th>
              <th>Product Name</th>
              <th>Price</th>
              <th>Status</th>
              <th>Date Added</th>
              <th>Actions</th>
            </tr>
          </thead>
          <tbody>
            {paginatedProducts.length ? (
              paginatedProducts.map((product, index) => (
                <tr key={product.id}>
                  <td>{(currentPage - 1) * itemsPerPage + index + 1}</td>
                  <td>{product.name}</td>
                  <td>{product.price}</td>
                  <td>
                    <span className={`${styles.statusBadge} ${styles[product.status.toLowerCase()]}`}>
                      {product.status}
                    </span>
                  </td>
                  <td>{product.addedDate}</td>
                  <td>
                    <Button
                      variant="outline-info"
                      size="sm"
                      className={styles.actionButton}
                      onClick={() => alert("View details")}
                    >
                      View
                    </Button>
                    <Button
                      variant="outline-success"
                      size="sm"
                      className={`${styles.actionButton} ms-2`}
                      onClick={() => alert("Edit product")}
                    >
                      Edit
                    </Button>
                    <Button
                      variant="outline-danger"
                      size="sm"
                      className={`${styles.actionButton} ms-2`}
                      onClick={() => handleDeleteProduct(product.id)}
                    >
                      Delete
                    </Button>
                  </td>
                </tr>
              ))
            ) : (
              <tr>
                <td colSpan="6" className="text-center">
                  No products available.
                </td>
              </tr>
            )}
          </tbody>
        </Table>

        {/* Pagination */}
        <PaginationComponent
          totalItems={filteredProducts.length}
          itemsPerPage={itemsPerPage}
          currentPage={currentPage}
          onPageChange={handlePageChange}
        />
      </div>

      {/* Add Product Modal */}
      <Modal show={showModal} onHide={handleCloseModal} centered>
        <Modal.Header closeButton>
          <Modal.Title>Add New Product</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form>
            <Form.Group className="mb-3" controlId="formProductName">
              <Form.Label>Product Name</Form.Label>
              <Form.Control
                type="text"
                placeholder="Enter product name"
                name="name"
                value={newProduct.name}
                onChange={handleNewProductChange}
              />
            </Form.Group>
            <Form.Group className="mb-3" controlId="formProductPrice">
              <Form.Label>Price</Form.Label>
              <Form.Control
                type="text"
                placeholder="Enter price"
                name="price"
                value={newProduct.price}
                onChange={handleNewProductChange}
              />
            </Form.Group>
            <Form.Group className="mb-3" controlId="formProductStatus">
              <Form.Label>Status</Form.Label>
              <Form.Select
                name="status"
                value={newProduct.status}
                onChange={handleNewProductChange}
              >
                <option value="Active">Active</option>
                <option value="Inactive">Inactive</option>
              </Form.Select>
            </Form.Group>
          </Form>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleCloseModal}>
            Cancel
          </Button>
          <Button variant="primary" onClick={handleAddProduct}>
            Add Product
          </Button>
        </Modal.Footer>
      </Modal>
    </Layout>
  );
};

export default ProductPage;
